
@import "@/style/common/var.scss";
.ins-step {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    flex: 1;
    overflow: hidden;
    &:last-child {
        flex: none;
        .ins-step__line {
            display: none;
        }
    }
    &__line {
        width: 100%;
        padding: 0 10px;
        position: absolute;
        left: 0;
        top: 13px;
        &-status {
            display: inline-block;
            width: 100%;
            height: 1px;
            vertical-align: top;
            background: #e8eaec;
            border-radius: 1px;
            position: relative;
        }
    }
    &__icon {
        display: inline-block;
        margin-left: -10px;
        padding-left: 10px;
        position: relative;
        vertical-align: top;
        background-color: #fff;
        &-inner {
            display: inline-block;
            width: 26px;
            height: 26px;
            line-height: 26px;
            margin-right: 8px;
            text-align: center;
            border: 1px solid #ccc;
            border-radius: 50%;
            font-size: 14px;
            transition: background-color 0.2s ease-in-out;
            &-process {
                border-color: $ins--color-primary;
                background-color: $ins--color-primary;
                color: $ins--color-white;
            }
            &-finish {
                border-color: $ins--color-primary;
                color: $ins--color-primary;
            }
            &-success {
                border-color: $ins--color-success;
                color: $ins--color-success;
            }
            &-error {
                border-color: $ins--color-danger;
                color: $ins--color-danger;
            }
        }
    }
    &__main {
        display: inline;
        background-color: #fff;
        vertical-align: top;
        position: relative;
        &-title {
            display: inline-block;
            margin-bottom: 4px;
            padding-right: 10px;
            font-size: 14px;
            font-weight: 700;
            color: #999;
            background-color: #fff;
            &-process {
                color: $ins--color-text-regular;
            }
            &-finish {
                color: $ins--color-primary;
            }
            &-success {
                color: $ins--color-success;
            }
            &-error {
                color: $ins--color-danger;
            }
        }
        &-desc {
            padding-left: 35px;
            color: #999;
            font-size: 12px;
        }
    }
}
.ins-steps-horizontal {
    .ins-step__line {
        box-sizing: border-box;
        display: inline-block;
        width: 100%;
        padding: 0 10px;
        position: absolute;
        left: 0;
        top: 13px;
        height: 1px;
        &-status {
            display: inline-block;
            width: 100%;
            height: 1px;
            vertical-align: top;
            background: #e8eaec;
            border-radius: 1px;
            position: relative;
            &::after {
                content: "";
                width: 0;
                height: 100%;
                background: #e8eaec;
                opacity: 0;
                position: absolute;
                top: 0;
            }
            &-wait,
            &-process {
                &::after {
                    width: 100%;
                    transition: all 0.2s ease-in-out;
                    opacity: 1;
                    background-color: #e8eaec;
                }
            }
            &-finish {
                &::after {
                    width: 100%;
                    transition: all 0.2s ease-in-out;
                    opacity: 1;
                    background-color: $ins--color-primary;
                }
            }
            &-success {
                &::after {
                    width: 100%;
                    transition: all 0.2s ease-in-out;
                    opacity: 1;
                    background-color: $ins--color-success;
                }
            }
            &-error {
                &::after {
                    width: 100%;
                    transition: all 0.2s ease-in-out;
                    opacity: 1;
                    background-color: $ins--color-danger;
                }
            }
        }
    }
}
.ins-steps-vertical {
    .ins-step {
        display: block;
        overflow: visible;
        &__line {
            position: absolute;
            left: 13px;
            top: 0;
            height: 100%;
            width: 1px;
            padding: 30px 0 4px;
            &-status {
                height: 100%;
                width: 1px;
                &::after {
                    content: "";
                    width: 0;
                    height: 100%;
                    background-color: #e8eaec;
                    opacity: 0;
                    position: absolute;
                    top: 0;
                }
                &-wait,
                &-process {
                    &::after {
                        width: 100%;
                        transition: all 0.2s ease-in-out;
                        opacity: 1;
                        background-color: #e8eaec;
                    }
                }
                &-finish {
                    &::after {
                        width: 100%;
                        transition: all 0.2s ease-in-out;
                        opacity: 1;
                        background-color: $ins--color-primary;
                    }
                }
                &-success {
                    &::after {
                        width: 100%;
                        transition: all 0.2s ease-in-out;
                        opacity: 1;
                        background-color: $ins--color-success;
                    }
                }
                &-error {
                    &::after {
                        width: 100%;
                        transition: all 0.2s ease-in-out;
                        opacity: 1;
                        background-color: $ins--color-danger;
                    }
                }
            }
        }
        &__main {
            &-desc {
                margin-bottom: 14px;
            }
        }
    }
}
