
.ins-rate {
    display: flex;
    &__icon {
        &-item {
            margin: 0 2px;
            position: relative;
            display: inline-block;
            cursor: pointer;
            font-size: 16px;
            &-half {
                display: block;
                width: 0;
            }
        }
        &-checked {
            color: #f5a623;
        }
    }
    &__text {
        margin-left: 10px;
    }
}
