
.ins-layout {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex: 1;
    flex-basis: auto;
    box-sizing: border-box;
    min-width: 0;
    &.is-vertical {
        flex-direction: row;
    }
    &.is-horizontal {
        flex-direction: column;
    }
}
