
@import "@/style/common/var.scss";
.ins-progress {
    overflow: hidden;
    line-height: 1;
    box-sizing: border-box;
    position: relative;
    &-bar {
        padding-right: 50px;
        display: inline-block;
        vertical-align: middle;
        width: 100%;
        margin-right: -55px;
        box-sizing: border-box;
        &.is-text-nside {
            padding-right: 0;
        }
        &__outer {
            height: 6px;
            border-radius: 100px;
            background-color: #ebeef5;
            overflow: hidden;
            position: relative;
            vertical-align: middle;
        }
        &__inner {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            background-color: #409eff;
            text-align: right;
            border-radius: 100px;
            line-height: 1;
            white-space: nowrap;
            transition: width 0.6s ease;
            &-text {
                display: inline-block;
                vertical-align: text-bottom;
                color: #fff;
                font-size: 12px;
                margin: 0 5px;
            }
        }
    }
    &__text {
        font-size: 14px;
        color: #606266;
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;
        line-height: 1;
        box-sizing: border-box;
    }
}
.ins-progress.is-success {
    .ins-progress-bar__inner {
        background-color: $ins--color-success;
    }
    .ins-progress__text {
        background-color: $ins--color-success;
        border-radius: 50%;
        color: $ins--color-white;
        [class^="ins-icon-"] {
            transform: scale(0.8);
        }
    }
}
.ins-progress.is-danger {
    .ins-progress-bar__inner {
        background-color: $ins--color-danger;
    }
    .ins-progress__text {
        background-color: $ins--color-danger;
        border-radius: 50%;
        color: $ins--color-white;
        [class^="ins-icon-"] {
            transform: scale(0.8);
        }
    }
}
.ins-progress--circle.is-success {
    .ins-progress-bar__inner {
        background-color: $ins--color-success;
    }
    .ins-progress__text {
        background-color: transparent;
        border-radius: 0;
        color: $ins--color-success;
        [class^="ins-icon-"] {
            transform: scale(1.2);
        }
    }
}
.ins-progress--circle.is-danger {
    .ins-progress-bar__inner {
        background-color: $ins--color-danger;
    }
    .ins-progress__text {
        background-color: transparent;
        border-radius: 0;
        color: $ins--color-danger;
        [class^="ins-icon-"] {
            transform: scale(1.2);
        }
    }
}
.ins-progress--circle {
    display: inline-block;
    .ins-progress__text {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        text-align: center;
        margin: 0;
        transform: translateY(-50%);
    }
}
