
@import "@/style/common/var.scss";
.ins-breadcrumb-item {
    & > a {
        &:hover {
            color: $ins--color-primary;
        }
    }
    &-text {
        text-decoration: none;
        color: #515a6e;
        transition: color 0.2s ease-in-out;
    }
    &-symbol {
        margin: 0 3px 0 2px;
    }
    &:last-child {
        font-weight: 700;
        color: #515a6e;
        .ins-breadcrumb-item-symbol {
            display: none;
        }
    }
}
