
@import "@/style/common/var.scss";

.ins-radio {
    color: #606266;
    font-weight: 500;
    line-height: 1;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    outline: none;
    font-size: 14px;
    margin-right: 30px;
    user-select: none;
    &__input {
        white-space: nowrap;
        cursor: pointer;
        outline: none;
        display: inline-block;
        line-height: 1;
        position: relative;
        vertical-align: middle;
    }
    &__inner {
        border: 1px solid $ins--radio-border-color;
        border-radius: 100%;
        width: 14px;
        height: 14px;
        background-color: $ins--color-white;
        position: relative;
        cursor: pointer;
        display: inline-block;
        box-sizing: border-box;
        &:after {
            width: 4px;
            height: 4px;
            border-radius: 100%;
            background-color: #fff;
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%) scale(0);
            transition: transform 0.15s ease-in;
        }
    }
    &__input.is-checked {
        & + .ins-radio__label {
            color: $ins--color-primary;
        }
        .ins-radio__inner {
            border-color: $ins--color-primary;
            background: $ins--color-primary;
            &:after {
                transform: translate(-50%, -50%) scale(1);
            }
        }
    }
    &__input.is-disabled {
        & + .ins-radio__label {
            color: $ins--checkbox-text-color-disabled;
            cursor: not-allowed;
        }
        .ins-radio__inner {
            background-color: $ins--checkbox-background-color-disabled;
            border-color: #e4e7ed;
            cursor: not-allowed;
            &:after {
                cursor: not-allowed;
                background-color: $ins--checkbox-background-color-disabled;
            }
        }
    }
    &__input.is-checked.is-disabled {
        .ins-radio__inner {
            background-color: #f5f7fa;
            border-color: #e4e7ed;
            &:after {
                background-color: #c0c4cc;
            }
        }
    }
    &__label {
        font-size: 14px;
        padding-left: 10px;
    }
}
